/**
* 文书类型组件
* @module 案件
* @desc 查看文书
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                center
                width="70%"
        >
            <table class="gridTable" v-loading="loading">
                <tr>
                    <td class="label">案件类型：</td>
                    <td>
                        <span>{{form.caseType | caseType}}</span>
                    </td>
                    <td class="label">案件流程：</td>
                    <td>
                        <span>{{form.caseProcess|caseProcess}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">文书名称：</td>
                    <td colspan="4">
                        <span>{{form.docName}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">是否上传/制作：</td>
                    <td>
                        <span>{{form.makeUpload | makeUpload}}</span>
                    </td>
                    <td class="label">上传类型：</td>
                    <td>
                        <span>{{form.uploadType |uploadType }}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">文件最大值：</td>
                    <td>
                        <span>{{form.uploadMaxSize+'M'}}</span>
                    </td>
                    <td class="label">文书图标样式：</td>
                    <td>
                        <span>{{form.docStyle }}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">是否必传：</td>
                    <td>
                        <span>{{form.isMust | YESORNO}}</span>
                    </td>
                    <td class="label">是否证据：</td>
                    <td colspan="4">
                        <span>{{form.isEvidence|YESORNO}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">备注：</td>
                    <td colspan="4">
                        <span>{{form.remark}}</span>
                    </td>
                </tr>
            </table>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiDocumentTypeGet} from "../../../api/case";
    import {YESORNO} from "../../../until/wordBook";

    let that;
    export default {
        name: "see",
        props: {
            caseTypeList: {
                type: Array,
                default: []
            },
            caseProcessList: {
                type: Array,
                default: []
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                loading: true,
                form: {
                    typeName: "",
                    isEvidence: "",
                    isUpload: "",
                    isMake: ""
                }
            };
        },
        methods: {
            //弹窗关闭
            closeDialog() {
                this.$emit("update:show", false);
            },

            // 提交
            submitForm() {
                this.closeDialog();
            },

            //弹框弹出时获取data给form
            initPage() {
                if (this.type) {
                    apiDocumentTypeGet(this.type).then(res => {
                        this.form = res.data;
                        this.loading = false;
                    });
                }
            }
        },
        beforeCreate() {
            that = this;
        },
        filters: {
            //是否？
            YESORNO(val) {
                return YESORNO[val];
            },
            // 上传类型
            uploadType(val) {
                return val == "video"
                    ? "视频"
                    : val == "img"
                        ? "图片"
                        : val == "audio"
                            ? "音频"
                            : "";
            },
            //制作类型
            makeUpload(val) {
                return val == "1" ? "上传" : val == "2" ? "制作" : "";
            },
            //文书类型
            docStyle(val) {
                that.docStyleArr.forEach(item => {
                    if (val == item.dictValue) {
                        return item.dictName;
                    }
                });
            },
            //案件过程
            caseProcess(val) {
                if (
                    that.caseProcessList.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.caseProcessList.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            },
            //加M
            uploadMaxSize(val) {
                return val + "M";
            },
            //案件类型
            caseType(val) {
                if (
                    that.caseTypeList.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.caseTypeList.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }
</style>


