/**
* 文书类型组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="form-plu">
            <lj-search-form :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column align="center" label="案件类型" prop="caseType" width="80">
                <template slot-scope="scope">{{scope.row.caseType | caseType}}</template>
            </el-table-column>
            <el-table-column align="center" label="案件流程" prop="caseProcess" width="130" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.caseProcess | caseProcess}}</template>
            </el-table-column>
            <el-table-column align="center" label="文书名称" prop="docName" show-overflow-tooltip/>

            <el-table-column align="center" label="上传/制作" prop="makeUpload" width="100">
                <template slot-scope="scope">{{scope.row.makeUpload | uploadMake}}</template>
            </el-table-column>
            <el-table-column align="center" label="上传类型" prop="uploadType" width="80">
                <template slot-scope="scope">{{scope.row.uploadType | uploadType}}</template>
            </el-table-column>
            <el-table-column align="center" label="上传文件最大值" prop="uploadMaxSize" width="110">
                <template slot-scope="scope">{{scope.row.uploadMaxSize | uploadMaxSize}}</template>
            </el-table-column>
            <el-table-column align="center" label="是否证据" prop="isEvidence" width="80">
                <template slot-scope="scope">{{scope.row.isEvidence | YESORNO}}</template>
            </el-table-column>
            <el-table-column align="center" label="是否必须上传" prop="isMust" width="100">
                <template slot-scope="scope">{{scope.row.isMust | YESORNO}}</template>
            </el-table-column>
            <!-- <el-table-column align="center" label="备注" prop="remark" show-overflow-tooltip/> -->

            <el-table-column align="center" label="操作" width="120">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-search btn-css"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="编辑" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="editFunction(scope.$index, scope.row)"
                                icon="el-icon-edit"
                                type="primary"
                                v-if="buttonCheck('edit')"
                        ></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="delateFunction(scope.$index, scope.row)"
                                class="el-icon-delete btn-css"
                                type="danger"
                                v-if="buttonCheck('delete')"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="i-page"
        />

        <!-- 编辑&添加弹框 -->
        <change-edit
                :caseProcessList="caseProcessList"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.editId"
                @getList="getList"
        />
        <!-- 查看 -->
        <see-content
                :caseProcessList="caseProcessList"
                :caseTypeList="caseTypeList"
                :show.sync="seeObj.show"
                :title="seeObj.title"
                :type="seeObj.editId"
        />
    </div>
</template>

<script>
    import LjPagination from "../../../components/LjPagination";
    import LjSearchForm from "../../../components/LjSearchForm";
    import {YESORNO} from "../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seecontent";
    import {apiDocumentTypedelete, apiDocumentTypeSearch} from "../../../api/case";

    let that;
    export default {
        name: "documentTypePage",
        data() {
            return {
                caseProcessList: [],
                docStyleArr: [],
                loading: true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                caseTypeList: [],
                //搜索组件数据
                searchObj: [
                    {
                        type: "select",
                        name: "caseType",
                        options: [
                            {value: "normal", label: "一般案件"},
                            {value: "simple", label: "简易案件"}
                        ],
                        label: "案件类型"
                    },
                    {
                        type: "select",
                        name: "caseProcess",
                        options: [
                            {value: "normal", label: "一般案件"},
                            {value: "simple", label: "简易案件"}
                        ],
                        label: "案件流程"
                    }

                ],
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                list: []
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchForm,
            seeContent
        },

        methods: {
            //查询+搜索
            getList() {
                this.loading = true;
                apiDocumentTypeSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.docTypeId;
                this.seeObj.title = "查看";
                this.seeObj.show = true;
            },

            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = "新增文书";
                this.editObj.show = true;
            },

            //编辑
            editFunction(index, row) {
                this.editObj.editId = row.docTypeId;
                this.editObj.title = "修改";
                this.editObj.show = true;
            },

            //删除按钮事件
            delateFunction(index, data) {
                this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    apiDocumentTypedelete(data.docTypeId).then(() => {
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                    });
                });
            },

            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                apiDocumentTypeSearch(this.form).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        beforeCreate() {
            that = this;
        },
        //过滤器
        filters: {
            //是否
            YESORNO(val) {
                return YESORNO[val];
            },
            //上传类型
            uploadType(val) {
                return val == "video"
                    ? "视频"
                    : val == "img"
                        ? "图片"
                        : val == "audio"
                            ? "音频"
                            : "";
            },
            //上传还是制作
            uploadMake(val) {
                return val == "1" ? "上传" : val == "2" ? "制作" : "";
            },
            //根据value查name
            docStyle(val) {
                that.docStyleArr.forEach(item => {
                    if (val == item.dictValue) {
                        return item.dictName;
                    }
                });
            },
            //案件阶段
            caseProcess(val) {
                if (
                    that.caseProcessList.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.caseProcessList.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            },
            //拼接M
            uploadMaxSize(val) {
                return val + "M";
            },
            //案件类型
            caseType(val) {
                if (
                    that.caseTypeList.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.caseTypeList.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            }
        },

        created() {
            this.getList();
            //证据类型字典
            this.$until.workBookApi("upLoadType").then(res => {
                this.docStyleArr = res;
            });
            //案件类型字典
            this.$until.workBookApi("caseType").then(res => {
                this.caseTypeList = res;
            });
            //案件流程字典
            this.$until.workBookApi("caseProcess").then(res => {
                this.caseProcessList = res;
                let arr = [];
                res.forEach(item => {
                    let obj={
                        value:item.dictValue,
                        label:item.dictName
                    };
                    arr.push(obj);
                });
                this.searchObj[1].options = arr;
            });
        }
    };
</script>

<style scoped>
    .form-plu {
        margin-bottom: 10px
    }

    .btn-css {
        margin: 0 10px
    }

    .i-page {
        margin-top: 10px
    }
</style>
