/**
* 文书类型组件
* @module 案件
* @desc 添加和修改
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                center
                width="650px"
        >
            <el-form :model="form" :rules="rules" label-width="140px" ref="ruleForm" size="medium" v-loading="loading">
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="案件类型：" prop='caseType'>
                            <el-radio-group v-model="form.caseType">
                                <el-radio class="radio" label="normal">一般案件</el-radio>
                                <el-radio class="radio" label="simple">简易案件</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item :prop="form.caseType=='normal'?'caseProcess':''" label="案件流程："
                                      v-if="form.caseType=='normal'">
                            <el-select placeholder="请选择案件流程" v-model="form.caseProcess">
                                <el-option
                                        :key="item.dictValue"
                                        :label="item.dictName"
                                        :value="item.dictValue"
                                        v-for="item in caseProcessList"
                                ></el-option>
                            </el-select>

                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="文书名称：" prop="docName">
                            <el-input placeholder="请输入文书名称" v-model="form.docName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="文书图标样式：" >
                            <el-input class="book-ipt" placeholder="请输入图标样式" v-model="form.docStyle"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="是否上传/制作：" prop='makeUpload'>
                            <el-radio-group v-model="form.makeUpload">
                                <el-radio class="radio" label="1">上传</el-radio>
                                <el-radio class="radio" label="2">制作</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item :prop="form.makeUpload=='1'?'uploadType':''" label="上传类型："
                                      v-if="form.makeUpload=='1'">
                            <el-radio-group v-model="form.uploadType">
                                <el-radio class="radio" label="img">图片</el-radio>
                                <el-radio class="radio" label="video">视频</el-radio>
                                <el-radio class="radio" label="audio">音频</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item :prop="form.makeUpload=='1'?'uploadMaxSize':''" label="文件最大值："
                                      v-if="form.makeUpload=='1'">
                            <el-input
                                    class="ipt-up"
                                    placeholder="上传文件最大值"
                                    v-model="form.uploadMaxSize"
                                    vlaue="100"
                            ></el-input>
                            ( 单位：MB )
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="是否必传：" prop="isMust">
                            <el-radio-group v-model="form.isMust">
                                <el-radio class="radio" label="1">是</el-radio>
                                <el-radio class="radio" label="0">否</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="是否证据：" prop="isEvidence">
                            <el-switch :active-value="1" :inactive-value="0" v-model="form.isEvidence"></el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="备注信息：">
                            <el-input
                                    :autosize="{ minRows: 2, maxRows: 4}"
                                    placeholder="请输入备注"
                                    type="textarea"
                                    v-model="form.remark"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiDocumentTypeAdd, apiDocumentTypeGet, apiDocumentTypeUpdate} from "../../../api/case";

    export default {
        name: "edit",
        props: {
            //证据类型数组
            // docStyleArr:{
            //    type:Array,
            //    default:[]
            // },
            caseProcessList: {
                type: Array,
                default: []
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            }
        },

        data() {
            return {
                docStyleArr: [
                    {value: 1, label: "书证"},
                    {value: 2, label: "物证"},
                    {value: 3, label: "视听材料"},
                    {value: 4, label: "证人证言"},
                    {value: 5, label: "当事人的陈述"},
                    {value: 6, label: "鉴定结论"},
                    {value: 7, label: "勘验笔录"}
                ],
                loading: true,
                form: {
                    typeName: "",
                    isEvidence: "",
                    isUpload: "",
                    isMake: ""
                },
                options: [],
                value: "",
                rules: {
                    caseType: [
                        {required: true, message: '请选择案件类型', trigger: 'change'},
                    ],
                    makeUpload: [
                        {required: true, message: '请选择上传还是制作', trigger: 'change'},
                    ],
                    isEvidence: [
                        {required: true, message: '请选择是否证据', trigger: 'change'},
                    ],
                    docName: [
                        {required: true, message: '请输入文书名称', trigger: 'blur'},
                    ],
                    docStyle: [
                        {required: true, message: '请输入文书图标样式', trigger: 'blur'},
                    ],
                    isMust: [
                        {required: true, message: '请选择是否必传', trigger: 'change'},
                    ],
                    caseProcess: [
                        {required: true, message: '请选择案件流程', trigger: 'change'},
                    ],
                    uploadType: [
                        {required: true, message: '请选择上传类型', trigger: 'change'},
                    ],
                    uploadMaxSize: [
                        {required: true, message: '请输入上传文件最大值', trigger: 'change'},
                    ]
                }
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //提交
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (this.form.makeUpload == 2) {
                        this.form.uploadType = "";
                        this.form.uploadMaxSize = "";
                    }
                    if (this.form.caseType == "simple") {
                        this.form.caseProcess = "";
                    }
                    if (valid) {
                        if (this.type) {
                            //编辑
                            this.loading = true;
                            apiDocumentTypeUpdate(this.form).then(res => {
                                this.$emit("getList");
                                this.$message({
                                    type: "success",
                                    message: "修改成功!"
                                });
                                this.loading = false;
                                this.closeDialog();
                            });
                        } else {
                            //添加
                            this.loading = true;
                            apiDocumentTypeAdd(this.form).then(res => {
                                this.$emit("getList");
                                this.$message({
                                    type: "success",
                                    message: "添加成功!"
                                });
                                this.loading = false;
                                this.closeDialog();
                            });
                        }
                    }
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                if (this.type) {
                    apiDocumentTypeGet(this.type).then(res => {
                        this.form = res.data;
                        this.loading = false;
                        this.form.makeUpload = String(this.form.makeUpload);
                        this.form.isMust = String(this.form.isMust);
                    });
                } else {
                    this.loading = false;
                    this.form = {
                        isDisable: 1,
                        enforceOrder: 0,
                        typeName: "",
                        isEvidence: 0,
                        isUpload: 0,
                        isMake: 0
                    };
                }
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            },
            //监听uploadType参数
            "form.uploadType"(value) {
                if (value) {
                    this.form.uploadMaxSize =
                        value == "img"
                            ? "10"
                            : value == "video"
                            ? "100"
                            : value == "audio"
                                ? "20"
                                : "";
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .ipt-up {
        width: 150px;
    }

    .book-ipt {
        width: 200px;
    }
</style>


